#root,
body,
html {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

html {
  font-size: 16px;
}

body {
  margin: 0;
  padding: 0;
  font-weight: $font-weight-base;
  font-size: $font-size-base;
  line-height: 1.5;
  font-family: $font-family-base;
}

div,
a {
  box-sizing: border-box;
}

img {
  max-width: 100%;
}
h1,
h2,
h3,
h4,
h5,
h6,
.card-title {
  color: $text-body !important;
  // font-weight: 500;
}

.layout1,
.layout2,
.MuiPaper-root,
.MuiTableCell-body,
.egret-customizer {
  color: $text-body !important;
}

//Lùi menu con
.navigation .submenu .nav-item {
  margin-left: 10px;
}

.navigation .nav-item:hover,
.navigation .nav-item:active{
  color: #CCFF99  !important; 
  background-color: rgba(0, 0, 0, 0.524) !important;
  padding-left: 5px !important;
}

